const Router = require('koa-router');
const router = new Router({ prefix: '/api' });
const { name: service, version } = require('../../../../package.json');
const { formResponse } = require('../../middleware/koa-response');
const axios = require('axios');
const https = require('https');

router.use(formResponse());

router.get('/', async (ctx) => {
	const _now = new Date();
	ctx.body = `${service} works! start at ${_now} version: ${version}`;
	ctx.status = 200;
});

router.post('/portal', async (ctx) => {
	try {
		console.log('request come in  ....')
		let { username, password } = ctx.request.body;
		let acUrl = 'http://172.16.28.1:8000/login'

		const options = {
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded',
				'Access-Control-Allow-Origin': '*'
			}
		}

		let result = await axios.post(acUrl, { username, password }, options);
		console.log(result)
	} catch (error) {
		console.log('request error:' + error)
	}
})

module.exports = router;